@import './../../style.scss';

.page-container{
    margin: auto 5px;
    .title {
        color: $fontColorStrong;
        font-size: 24px;
        margin-top: 30px;
        margin-bottom: 10px;
        text-align: center;
    }
    .seperator {
        width: 70px;
        height: 4px;
        background: gray;
        margin: auto;
    }
}
table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  td, th {
    border: 1px solid $borderGray;
    text-align: left;
    padding: 8px;
  }
  
  tr:nth-child(even) {
    background-color: $borderGray;
  }
